import { Link } from 'react-router-dom'

import BtnLogin from '../../../../UI/BtnLogin'
import InputLogin from '../../../../UI/InputLogin'

import s from './RegisterTab.module.sass'

const RegisterTab = () => {
	return (
		<form action='#'>
			<InputLogin label='' type='text' />
			<InputLogin label='' type='text' />
			<InputLogin label='' type='number' />
			<InputLogin label=' ' type='password' />
			<label className={s.checkboxLabel}>
				<input type="checkbox" className='checkboxInput' />
				<span>
					   <Link to="/board-games/policy"> </Link>
					    
				</span>
			</label>
			<label className={s.checkboxLabelBottom}>
				<input type="checkbox" className='checkboxInput' />
				<span>        </span>
			</label>
			<BtnLogin text={''} />
		</form>
	)
}

export default RegisterTab